import { FlexPlugin } from 'flex-plugin';
import React from 'react';
import { TweetDisplay } from './TweetDisplay';
import IFrame from 'react-iframe';

const PLUGIN_NAME = 'PluginDemo';

export default class PluginDemo extends FlexPlugin {
  constructor() {
    super(PLUGIN_NAME);
  }

  /**
   * This code is run when your plugin is being started
   * Use this to modify any UI components or attach to the actions framework
   *
   * @param flex { typeof import('@twilio/flex-ui') }
   * @param manager { import('@twilio/flex-ui').Manager }
   */
  init(flex, manager) {
    // flex.AgentDesktopView.Panel1.Content.add(
    //   <CustomTaskListComponent key="demo-component" text="Good job accepting the task!"/>,
    //   {
    //     sortOrder: -1,
    //   }
    // );
    // flex.TaskCanvas.Content.add(
    //   <CustomTaskListComponent key="demo-component" text="Good job accepting the task!"/>,
    //   {
    //     sortOrder: -1,
    //   }
    // );
    flex.Actions.addListener('afterAcceptTask', (payload) => {
      const taskAttributes = payload.task && payload.task.attributes;
      const taskType = taskAttributes && taskAttributes.type;
      if (taskType && taskType == 'twitter') {
        flex.TaskInfoPanel.Content.add(<TweetDisplay key='tweet-display' tweetId={taskAttributes.tweetId}/>);
      }
    });
    // flex.Actions.addListener('afterCompleteTask', (payload) => {
    //   flex.AgentDesktopView.Panel1.Content.remove('demo-component');
    // })
    flex.CRMContainer.defaultProps.uriCallback = (task) => {
      return task && task.attributes.productId ? `https://localhost:3000/product/${task.attributes.productId}` : 'https://localhost:3000/';
    }
  }
}



