import React from 'react';
import { TwitterTweetEmbed } from 'react-twitter-embed';

export class TweetDisplay extends React.Component {
  render() {
    return (
      <div>
        <hr></hr>
        <TwitterTweetEmbed tweetId={this.props.tweetId}/>
      </div>
    )
  }
}


